header{
    background-color: #fff;
    /* height: 10vh; */
    box-shadow: 0 5px 30px rgb(0 22 84 / 10%);
    padding: 9px 0;
    position: sticky;
    top: 0;
    z-index: 999;
}

header img{
    width: 170px;
}

header a{
    margin-left: 30px;
    transition: 0.5s;
    font-weight: 500;
}
    
    

header a:hover{
    color: #27ae60;
}

/* .active{
    color: #27ae60;
} */

header span{
    width: auto;
    padding: 0 6px;
    background-color: #27ae60;
    border-radius: 50px;
    color: #fff;
    margin-right: 4px;
    font-weight: 400;
}

header h4{
    font-weight: 500;
    margin-right: 20px;
}

header .toggle{
    display: none;
}

@media screen and (max-width : 820px){
    header{
        padding: 0;
    }
    header img{
        padding: 8px 0;
    }
    header .button{
        display: none;
    }
    header .nav .flex{
        display: none;
    }
    header .toggle{
        display: block;
        margin: auto 0;
    }
    header .small{
        position: absolute;
        top: 5.5vh;
        left: 0;
        width: 100%;
        background-color: #27ae60;
        padding: 20px;
    }
    header li a{
        display: inline-block;
        margin-bottom: 20px;
        color: #fff;
    }
}

@media screen and (max-width : 768px){
    header .small{
        top: 6.5vh;
    }
}




